import React, { useRef, useState } from "react";
import {
  useGLTF,
  Float,
  Environment,
  PresentationControls,
  Center,
  Html,
  Text,
} from "@react-three/drei";
import { useFrame, useThree } from "@react-three/fiber";

function Experience() {
  const model = useGLTF("./model/model.gltf");
  console.log(model);

  const newref = useRef();

  useFrame((state, delta) => {
    // newref.current.rotation.x += delta;
  });

  const three = useThree();

  const [turn, setTurn] = useState(false);

  const changeLayout = () => {
    if (turn) {
      three.camera.position.set(0, 0, 1);
    } else {
      three.camera.position.set(0, 0, 5);
    }
    setTurn(!turn);
  };

  return (
    <>
      <Environment preset="city" />
      <Center>
        <PresentationControls
          global
          rotation={[0.13, 0.1, 0]}
          polar={[-0.4, 0.2]}
          azimuth={[-1, 0.75]}
          config={{ mass: 2, tension: 400 }}
          snap={{ mass: 4, tension: 400 }}
        >
          <Float rotationIntensity={0.4}>
            <primitive object={model.scene} scale={1} onClick={changeLayout}>
              <Html
                transform
                wrapperClass="htmlScreen"
                distanceFactor={1.17}
                ref={newref}
                position={[0, 1.56, -1.4]}
                rotation-x={-0.256}
              >
                <iframe
                  src="https://www.youtube.com/embed/dQw4w9WgXcQ"
                  title="Rick Astley - Never Gonna Give You Up (Official Music Video)"
                  //   frameborder="0"
                  allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                  //   allowfullscreen
                ></iframe>
              </Html>
            </primitive>
            <Text fontSize={1} position={[3, 1.75, 0.75]} rotation={[0, 5, 0]}>
              PortFolio
            </Text>
          </Float>
        </PresentationControls>
      </Center>
    </>
  );
}

export default Experience;
